.App {
    height: 100%;
    max-height: 100%;
    user-select: none;
}

.App .list {
    height: 100%;
    display: flex;
    flex-flow: column;
    gap: 10px;
    padding: 10px;
}

.App .list #empty {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2em;
    text-align: center;
    color: gray;
}

.App .item {
    min-height: 75px;
    display: flex;
    justify-content: space-between;
    background-color: gainsboro;
    font-size: 2em;
}

.App .item button {
    all: unset;
    box-sizing: border-box;
    height: 100%;
}

.App .item .title {
    padding-left: 10px;
    width: 100%;
}

.App .item .title .checkmark {
    padding: 0 10px 0 10px;
}

.App .item .remove {
    width: 50px;
    background-color: lightgray;
    text-align: center;
}

.App .add {
    all: unset;
    box-sizing: border-box;
    position: fixed;
    bottom: 10px;
    width:  75px;
    height: 75px;
    right: 10px;
    background-color: #32b9e6;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition-duration: 0.2s;
    padding: 20px;
}

.App .add img {
    width: 100%;
}

.App .add:active {
    box-shadow: 0 0 10px black;
}